.App {
  text-align: center;
}

.black-nav {
  display: flex;
  padding: 0 20px;
  color: #fff;
  background-color: #000;
}

.change-btn {
  margin: 20px 0;
}

.list {
  padding: 0 20px;
  text-align: left;
}
.list li {
  padding: 10px 0;
  border-bottom: 1px solid gray;
  list-style: none;
}
.list li span {
  cursor: pointer;
}
.modal {
  margin: 20px;
  padding: 20px;
  text-align: left;
  background-color: #eee;
}

h3, h4, p {margin: 0;}
h3 {display: inline-block; cursor: pointer;}